import { useRef, useState } from 'react';
import Slider from 'react-slick';
import "slick-carousel/slick/slick.css";

const SOLUTION = [
    { id: 1, content: " ", description: "H-Mobile Thru (  )       .", link: "/" },
    { id: 2, content: "  ", description: " ,       ,   .", link: "/" },
    { id: 3, content: " ", description: "   ,        .        .", link: "/" },
    { id: 4, content: " ", description: "H-Mobile Thru (  )       .", link: "/" },
    { id: 5, content: " ", description: "H-Mobile Thru (  )       .", link: "/" },
    { id: 6, content: " ", description: "H-Mobile Thru (  )       .", link: "/" },
    { id: 7, content: " ", description: "H-Mobile Thru (  )       .", link: "/" },
    { id: 8, content: " ", description: "H-Mobile Thru (  )       .", link: "/" },
    { id: 9, content: " ", description: "H-Mobile Thru (  )       .", link: "/" },
]

const Solution = () => {
    const [slideNum, setSlideNum] = useState(0);
    const SLIDE = useRef(null);
    return (
        <section className="Solution csc">
            <h2>  </h2>
            <p>   .</p>
            <div className="container">
                <Slider
                    centerMode={true}
                    centerPadding={'300px'}
                    afterChange={index => setSlideNum(index)}
                    arrows={false}
                    ref={SLIDE}
                    className={'solution_slide'}
                >
                    {
                        SOLUTION.map((img, idx) => {
                            return (
                                <figure key={img.id} className={slideNum === idx ? 'on' : ''}>
                                    <img src={process.env.PUBLIC_URL + "/assets/images/main_s0" + img.id + ".jpg"} alt="" />
                                </figure>
                            )
                        })
                    }
                </Slider>
                <div className="box">
                    <div className="content">
                        <div className='tit'>{SOLUTION[slideNum].content}</div>
                        <p>{SOLUTION[slideNum].description}</p>
                        <a href={SOLUTION[slideNum].link} className='cbtn'></a>
                    </div>
                    <div className="num">
                        0{slideNum + 1} <span>/ 0{SOLUTION.length}</span>
                    </div>
                </div>
                <div className="arrows">
                    <i className='xi-arrow-left' onClick={() => SLIDE.current.slickPrev()}></i>
                    <i className='xi-arrow-right' onClick={() => SLIDE.current.slickNext()}></i>
                </div>
                <ul className="dots">
                    {
                        SOLUTION.map((dot, idx) => <li
                            key={dot.id}
                            onClick={() => SLIDE.current.slickGoTo(idx)}
                            className={slideNum === idx ? 'on' : ''}
                        >
                            {dot.id}
                        </li>)
                    }
                </ul>
            </div>
        </section>
    )
}

export default Solution;