import { useState } from 'react';

const TopBanner = () => {
    const [TB, setTB] = useState('');
    return (
        <div className={'TopBanner ' + TB}>
            <div className="inner">
                <h2>    <span>Green Technology</span></h2>
                <p>       .</p>
                <i className='xi-close' onClick={() => setTB('on')}></i>
            </div>
        </div>
    )
}

export default TopBanner;