const NEWS = [
    { id: 1, category: "", title: ",     ", date: "2022.08.22", link: "/", src: "/assets/images/main_news01.jpg" },
    { id: 2, category: "", title: " 2030  Top 5 ", date: "2022.07.14", link: "/", src: "/assets/images/main_news02.jpg" },
    { id: 3, category: "", title: "2022 6 ", date: "2022 6", link: "/", src: "/assets/images/main_news03.jpg" },
]


const News = () => {
    return (
        <section className="News csc">
            <h2> </h2>
            <p>  .</p>
            <div className="inner">
                {
                    NEWS.map(news => {
                        return (
                            <figure key={news.id}>
                                <a href={news.link}>
                                    <div className="box">
                                        <img src={process.env.PUBLIC_URL + news.src} alt={news.title} />
                                    </div>
                                    <strong>{news.category}</strong>
                                    <div className="title">{news.title}</div>
                                    <span>{news.date}</span>
                                </a>
                            </figure>
                        )
                    })
                }
            </div>
        </section>
    )
}
export default News;